/* 챗봇 전체 스타일 */
.chatBot {

  position: fixed;
  right: 20px;
  bottom: 15px;
  z-index: 9999;
}

/* 챗봇 보이스 UI 스마일 스타일 */
.offSmile {

  background-image: url('../media/smile.svg');
  width: 50px;
  height: 50px;
  background-size: contain;
  border-Radius: 50%;
  border: none;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
  cursor: pointer;
  animation: siri-swing 1s linear infinite;
  -webkit-animation: siri-swing 1s linear infinite; /* Safari and Chrome */
  z-index: 9999;

}

@keyframes siri-swing {

  0% {

    transform: rotate(25deg);
    animation-timing-function: cubic-bezier(0.455, 0.030, 0.515, 0.955);
    animation-duration: 0.5s;
  }

  50% {

    transform: rotate(-25deg);
    animation-timing-function: cubic-bezier(0.455, 0.030, 0.515, 0.955);
    animation-duration: 0.8s;
  }

  100% {

    transform: rotate(25deg);
    animation-timing-function: cubic-bezier(0.455, 0.030, 0.515, 0.955);
    animation-duration: 0.8s;
  }
}

/* 챗봇 텍스트 UI 스마일 스타일 */
.onSmile {

  background-image: url('../media/smile.svg');
  width: 50px;
  height: 50px;
  background-size: contain;
  border-Radius: 50%;
  border: none;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.3);
  cursor: pointer;
  animation: siri-rotate 1s linear infinite;
  -webkit-animation: siri-rotate 1s linear infinite; /* Safari and Chrome */
  z-index: 9999;
}

@keyframes siri-rotate {

  0%  {

    transform: rotate(0deg);
    animation-timing-function: cubic-bezier(0.455, 0.030, 0.515, 0.955);
    animation-duration: 0.5s;
  }

  50% {

    transform: rotate(180deg);
    animation-timing-function: cubic-bezier(0.455, 0.030, 0.515, 0.955);
    animation-duration: 0.8s;
  }

  100%{

    transform: rotate(360deg);
    animation-timing-function: cubic-bezier(0.455, 0.030, 0.515, 0.955);
    animation-duration: 0.8s;
  }
}

.back-to-top {

  width: 50px;
  height: 50px;
  box-sizing: border-box;
  border: 3px solid black;
  border-Radius: 50%;
  background-color: #558B2F;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  transition: opacity 0.3s ease;
  margin-bottom: 10px;
  opacity: 0;
  z-index: 9999;
}

.back-to-top.visible {

  opacity: 1;
}

.back-to-top:hover {

  background-color: #555;
}

.botShow {

  width: 50px;
  height: 50px;
  background-color: #333;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
  font-family: 'Roboto', sans-serif;
  font-size: 15px;
  color: white;
  border: none;
  margin-bottom: 10px;
  z-index: 9999;

  /* 버튼 호버 효과 */
  transition: background-color 0.3s ease;
}

.botShow:hover {

  /* 호버 시 배경색 변경 */
  background-color: #ffac33;
  box-sizing: border-box;
  border: 3px solid black;
  cursor: pointer;
}