.virtualBot {
    position: fixed;
    bottom: 10px;
    right: 10px;
    width: 260px;
    height: 200px;
    padding: 5px;
    border: none;
    outline: none;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2);
    justify-content: center;
    margin-right: 70px;
    margin-bottom: 0;
    overflow: hidden;
    background-color: #f9f9f9;
    border-radius: 0 0 10px 10px;
    z-index: 9999;
}